<template>
  <div class="like">
    <!-- 顶部 -->
    <div class="like_top">
      <van-row>
        <van-col span="10">
          <van-icon name="arrow-left" class="icon" size="24" @click="upperLevel" color="#fff"/>
        </van-col>
        <van-col span="14">
          <h3>我的收藏</h3>
        </van-col>
      </van-row>
    </div>
    <!-- 顶部 end -->

    <template v-if="likeData.length > 0">
      <div class="like_box">
        <ul>
          <li v-for="(item,index) in likeData" :key="index" @click="goPage(item.pid)">
            <img :src="item.smallImg" alt />
            <h4>{{item.name}}</h4>
            <span>￥{{item.price}}</span>
            <van-icon name="delete" class="del" @click.stop="removeLike(item.pid, index)" />
          </li>
        </ul>
      </div>
    </template>

    <template v-else>
      <div>
        <div class="not-like">
          <div class="not-like-icon">
            <van-icon name="like" />
          </div>
          <div v-if="isLogin">没有收藏商品，赶紧去逛一逛！</div>
          <div v-else>
            <span class="login-text" @click="goState">登录，查看收藏数据</span>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";

import { utils } from "../utils/utils";

export default {
  naem: "Like",
  created() {
    // 进入前加载一次
    this.getLikeData();

    this.$store.state.vanTabbar = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.vanTabbar = true;
  },

  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    ...mapState("likeModule", ["likeData"])
  },

  methods: {
    upperLevel() {
      this.$router.go(-1);
    },

    goState() {
      this.$router.push({ name: "Login" });
    },

    // 获取我的收藏商品
    getLikeData() {
       let tokenString = localStorage.getItem('_t');
      //初始化数据
      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {

          this.$store.commit("changeData", {
            key: "isLogin",
            value: result.data.code != 700
          });

          if (result.data.code == 2000) {
            this.$store.commit("likeModule/changeData", {
              key: "likeData",
              value: result.data.result
            });
            
          }
        })
        .catch(err => {
          
        });
    },

    //查看商品详情
    goPage(pid) {
      this.$router.push({ name: "Details", params: { pid } });
    },

    //删除我的收藏商品
    removeLike(pid, index) {
      
       let tokenString = localStorage.getItem('_t');

      this.axios({
        method: "POST",
        url: "/notlike",
        data: {
          appkey: this.appkey,
          tokenString,
          pid
        },
        transformRequest: utils.transformRequest
      })
        .then(result => {
          
          this.$store.commit("likeModule/changeLikeData", index);
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
.like {
  .like_top {
    background-color: #0096FD;
    .icon {
      line-height: 40px;
    }
    h3 {
      line-height: 40px;
      color: #fff;
      font-weight: 400;
    }
  }
  .like_box {
    text-align: center;

    ul {
      li {
        float: left;
        width: calc(~"50% - 20px");
        margin: 10px;
        background-color: #f9f9f9;
        border-radius: 2px;
        box-shadow: 0px 0px 2px #ccc;
        img {
          width: 75%;
          height: 75%;
          margin-top: 8px;
          border-radius: 10px;
        }
        h4 {
          color: #333;
        }
        span {
          color: #333;
          font-size: 14px;
        }
        .del {
          margin-left: 50px;
          line-height: 1.5;
          font-size: 18px;
          color: #f50;
          margin-bottom: 8px;
        }
      }
    }
  }
  .not-like {
    font-size: 18px;
    margin-top: 50px;
    color: #e0e0e0;
    text-align: center;
  }
  .not-like-icon {
    font-size: 64px;
  }
  .login-text {
    color: #444;
  }
}
</style>